nav{
    display: grid;
    grid-template-columns: 1fr 3fr;
    align-items: center;
    padding: 1% 2%;
    background-color: aliceblue;
   }
   
   .Navbar-items{
     display: flex;
     flex-direction: row;
     justify-content: space-between;
     align-items: center;
     list-style-type: none;
   }
   .navbar-item{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
   }

   .material-symbols-outlined {
     font-variation-settings:
     'FILL' 0,
     'wght' 500,
     'GRAD' 0,
     'opsz' 84
   }
   .search-container {
    width: 350px;
    background-color: white;
    display: flex;
    align-items: center;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 5px;
  }
  
  .material-symbols-outlined {
    font-size: 24px; /* Adjust the size of the icon */
    color: #757575; /* Icon color */
  }
  
  .search-input {
    width: 100%;
    height: 30px;
    border: none;
    outline: none;
    flex-grow: 1; /* Allows the input field to take up the available space */
  }
  
  /* Optional: Style the input to have some space between the icon */
  .search-input::placeholder {
    padding-left: 2px;
  }
  
    a 
    {   width: 100%;
        display:flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        text-decoration: none;
        color: black;
        text-align: center;

    }
   